.page-section h2 {
flex: 0 0 20%;
}

.page-section div {
display: flex;
flex-wrap: wrap;
justify-content: center;
}

.page-section a {
margin-left: 20px;
color: blue;
text-decoration: none;
}

.project {
border: 5px solid gray;
min-height: 225px;
max-height: 225px;
text-decoration: none;
flex-basis: calc(49% - 1em);
display: flex;
align-items: flex-end;
margin-bottom: 10px;
margin-left: 5px;
margin-right: 5px;
}

.project:first-child, .project:nth-child(2) {
min-height: 400px;
flex-basis: 100%;
}

.project h3 {
background-color: grey;
margin: -22px 0 0 20px;
padding: 0;
/* display: inline; */
}

